<template>
  <div class="user_role">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home/hello' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <div class="adduser">
        <el-button type="primary" @click="dialogVisible = true" ></el-button>
      </div>
      <!--  -->
      <div class="add_tan">
        <el-dialog title="" :visible.sync="dialogVisible" width="60%" height="44%">
          <el-form label-width="80px" :model="from">
            <el-form-item label="">
              <el-input v-model="from.Name"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="from.Desc"></el-input>
            </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false"> </el-button>
            <el-button type="primary" @click="ajaxadd()"> </el-button>
          </span>
        </el-dialog>
      </div>
      <!--  -->
      <div class="rolelist">
        <template>
          <el-table :data="tableData" border style="width: 100%">
            <!--  -->
            <el-table-column type="expand" width="40">
              <template slot-scope="scope">
                <el-row :class="['dbottom',i1=== 0 ?'dbtop':'','center']" v-for="(item1,i1) in scope.row.children" :key="item1.id">
                  <!--  -->
                  <el-col :span="5">
                    <el-tag closable @close="open(scope.row,item1.id)">
                      {{ item1.authName }}
                    </el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <!--  -->
                  <el-col :span="19">
                    <el-row :class="[i2 === 0 ?'':'dbtop','center']" v-for="(item2,i2) in item1.children" :key="item2.id">
                      <el-col :span="6">
                        <el-tag type="success" closable @close="open(scope.row,item2.id)">{{ item2.authName }}</el-tag>
                        <i class="el-icon-caret-right"></i>
                      </el-col>
                      <el-col :span="18">
                        <el-tag closable type="warning" @close="open(scope.row,item3.id)" v-for="(item3) in item2.children" :key="item3.id">
                          {{ item3.authName }}
                        </el-tag>
                      </el-col>
                    </el-row>
                  </el-col>
                </el-row>
              </template>
            </el-table-column>
            <!--  -->
            <el-table-column type="index" label="#" width="40"></el-table-column>
            <el-table-column prop="roleName" label=""></el-table-column>
            <el-table-column prop="roleDesc" label=""></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                  <el-button type="primary" size="mini" icon="el-icon-edit" circle @click="ajaxedit(scope.row)"></el-button>
                  <el-button type="danger" size="mini" icon="el-icon-delete" circle @click="ajaxremove(scope.row.id)"></el-button>
                  <el-button type="warning" size="mini" icon="el-icon-star-off" circle @click="showSetRight(scope.row)"></el-button>    
              </template>
            </el-table-column>
          </el-table>
        </template>
      </div>
      <!--  -->
      <div class="role_edit">
        <el-dialog
          title=""
          :visible.sync="show"
          width="60%"
          >
          <el-form label-width="80px">
            <el-form-item label="">
              <el-input v-model="roleName"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="roleDesc"></el-input>
            </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button @click="show = false"> </el-button>
            <el-button type="primary" @click="ajaxasync()"> </el-button>
          </span>
        </el-dialog>
      </div>
      <!--  -->
      <div class="showSetRight">
        <el-dialog
          title=""
          :visible.sync="showRight"
          width="50%"
          @close="setRightClose()"
          >
          <!--  -->
          <el-tree :data="rightsList" :props="treeProps" show-checkbox node-key="id" default-expand-all
          :default-checked-keys="deyKeys"
          ref="treeRef" 
          ></el-tree>
          <span slot="footer" class="dialog-footer">
            <el-button @click="showRight = false"> </el-button>
            <el-button type="primary" @click="allotRights()"> </el-button>
          </span>
        </el-dialog>
      </div>
    </el-card>
  </div>
        </template>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialogVisible: false,
      from: {
        Name: "",
        Desc: ""
      },
      tableData: [],
      list:[],
      roleid:"",
      roleName:'',
      roleDesc:'',
      show:false,
      showRight:false,
      rightsList:[],
      treeProps:{
        label:'authName',
        children:'children'
      },
      // ID
      deyKeys:[],
      roleId:''
    };
  },
  created() {},
  mounted() {
    this.ajaxrole();
  },
  methods: {
    //
    ajaxrole() {
      this.$axios.get("/roles").then(res => {
        console.log(res);
        this.tableData = res.data.data;
      });
    },
    //    
    ajaxadd() {
      this.$axios.post('/roles',{"roleName":this.from.Name,"roleDesc":this.from.Desc}).then((msg)=>{
          console.log(msg)
          this.list = this.tableData
          this.list = msg
          this.ajaxrole()
      })
      this.dialogVisible = false;
      this.from.Name = this.from.Desc =""
    },
    // 
    ajaxremove(id){
      this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$axios.delete('/roles/' +id).then((res)=>{
            console.log(res)
            this.ajaxrole()
          })
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        });
    },
    // 
    ajaxedit(a){
      this.show = true
      this.roleid = a.id
      this.roleName = a.roleName
      this.roleDesc = a.roleDesc
    },
    // 
    ajaxasync(){
        this.$axios.put(`/roles/${this.roleid}`,{"roleName":this.roleName,"roleDesc":this.roleDesc}).then((res)=>{
          console.log(res)
          this.$message({
            message:'',
            type:'success',
          })
          this.ajaxrole()
          this.show = false
        })
    },
    // 
    async open(role,rightId) {
        let confrom =  await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).catch(err => err)
          if(confrom !=='confirm'){
            return this.$message.info('')
          }else{
            this.$message({
              message:'',
              type:'success'
            })
          }    
        let {data:res} =await this.$axios.delete(`roles/${role.id}/rights/${rightId}`)
          console.log(res)
          role.children = res.data 
      },
      // 
      async showSetRight(role){
        this.roleId = role.id
        let { data:res} = await this.$axios.get('rights/tree')
        console.log(res)
        if(res.meta.status !== 200){
          return this.$message.info('')
        }
        this.rightsList = res.data
        console.log(this.rightsList)
        this.getLeveKeys(role,this.deyKeys)
        this.showRight = true
      },
      // id
      getLeveKeys(node,arr){
        // children
        if(!node.children){
          return arr.push(node.id)
        }
        node.children.forEach(item=>this.getLeveKeys(item,arr))
      },
      // 
      setRightClose(){
        this.deyKeys = []
      },
      // 
      async allotRights(){
        let keys = [
          ...this.$refs.treeRef.getCheckedKeys(),
          ...this.$refs.treeRef.getHalfCheckedKeys()
        ]
        console.log(keys)
        let idStr = keys.join(`,`)
        console.log(idStr)
        let {data:res} = await this.$axios.post(`roles/${this.roleId}/rights`,{rids:idStr})
        console.log(res)
        this.$message({
          message:'',
          type:'success',
        })
        this.ajaxrole()
        this.showRight = false
      }
  }
};
</script>

<style scoped>
.el-tag{
  margin: 7px;
}
.el-row{
  width: 100%;
}
.dbtop{
  border-top: 1px solid #eee;
}
.dbottom{
  border-bottom: 1px solid #eee;
}
.cell .el-button.is-circle{
  width: 85px;
}
.box-card{
  margin-top: 15px;      
}
.el-dialog .el-form {
  bottom: 160px;
  position: static;
}
.el-table{
  margin-top: 20px;      
}
.center{
  display: flex;
  align-items: center;
}
.user_role .showSetRight .el-dialog{
  height: 45%;
}
</style>
