<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home/hello' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <div class="order_card">
      <el-card class="box-card">
        <!--  -->
        <div class="order_search">
          <el-input placeholder="" clearable @clear="ajaxorder" v-model="orderParams.query" class="input-with-select">
            <el-button slot="append" icon="el-icon-search" @click="ajaxorder"></el-button>
          </el-input>
        </div>
        <!--  -->
        <template>
          <el-table :data="tableData" border style="width: 100%">
            <el-table-column type="index" label="#" width="60"></el-table-column>
            <el-table-column prop="order_number" label="" width="260"></el-table-column>
            <el-table-column prop="order_price" label=""></el-table-column>
            <el-table-column prop="is_send" label="">
                <template slot-scope="scope">
                    <el-button type="danger" size="mini" v-if="scope.row.is_send == ''"></el-button>
                    <el-button type="primary" size="mini" v-if="scope.row.is_send == ''"></el-button>
                </template>
            </el-table-column>
            <el-table-column prop="is_send" label=""></el-table-column>
            <el-table-column prop="create_time" label="">
                <template slot-scope="scope">
                    {{ scope.row.create_time | timefn }}
                </template>
            </el-table-column>
            <el-table-column prop="date" label="">
                <template>
                    <el-button type="primary" size="mini" icon="el-icon-edit" circle @click="ajaxadderss"></el-button>
                    <el-button type="success" size="mini" icon="el-icon-location-outline" circle></el-button>
                </template>
            </el-table-column>
          </el-table>
        </template>
        <!--  -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="orderParams.pagenum"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="orderParams.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        ></el-pagination>
        <!--  -->
        <div class="adderss">
          <el-dialog
          title=""
          :visible.sync="adderss"
          width="50%"
          >
          <el-form>
            <el-form-item before label="/">
              <el-select placeholder="">
                <el-option label="" value="shanghai"></el-option>
                <el-option label="" value="beijing"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-input></el-input>
            </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button @click="adderss = false"> </el-button>
            <el-button type="primary" @click="adderss = false"> </el-button>
          </span>
        </el-dialog>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      orderParams: {
        query: "",
        pagenum: 1,
        pagesize: 5
      },
      tableData: [],
      total:0,
      adderss:false,
    };
  },
  created() {},
  mounted() {
    this.ajaxorder();
  },
  methods: {
    // 
    async ajaxorder() {
      let { data: res } = await this.$axios.get("/orders", {
        params: this.orderParams
      });
      console.log(res);
      this.tableData = res.data.goods;
      this.total = res.data.total
    },
    // 
    handleSizeChange(size) {
      this.orderParams.pagesize = size
      this.ajaxorder();
    },
    handleCurrentChange(page) {
      this.orderParams.pagenum = page
      this.ajaxorder();
    },
    // 
    ajaxadderss(){
      this.adderss = true
    }
  }
};
</script>

<stylescoped>
.order_search {
  display: flex;
  width: 35%;
}
.el-dialog .el-form{
    bottom: 140px;
}
.el-dialog .el-form .el-select{
  width: 100%;
}
</style>
