<template>
  <div class="">
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home/hello' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <el-alert
        title=""
        type="warning"
        effect="dark"
        show-icon
        :closable="false"
      >
      </el-alert>
      <!--  -->
      <el-row class="fl">
        <span></span>
        <el-cascader
        v-model="value"
         collapse-tags
          :options="list"
          :props="{ value: 'cat_id', label: 'cat_name',expandTrigger:'hover' }"
          @change="handleChange(value)"
        ></el-cascader>
      </el-row>
      <!--  -->
      <el-tabs type="border-card">
  <el-tab-pane label=""><!--  -->
<el-button type="primary" size="mini" :disabled="flag" @click="addActive"></el-button>
<!--  -->
 <el-table
        :data="listActive"
        style="width: 100%;margin-bottom: 20px;"
        border
      >
        <el-table-column type="expand" width="50">
        </el-table-column>
        <el-table-column type="index" label="#" width=""> </el-table-column>
        <el-table-column prop="attr_name" label="" width="">
        </el-table-column>
        <!--  -->
        <el-table-column label="" prop="attr_id" width="300">
          <template slot-scope="scope">
            <!--  -->
            <el-button size="mini" type="primary" icon="el-icon-edit" @click="update(scope.row)"></el-button>
            <!--  -->
            <el-button size="mini" type="danger" icon="el-icon-delete" @click="del(scope.row.attr_id)"></el-button>
            </template>
        </el-table-column>
      </el-table>
</el-tab-pane>
  <el-tab-pane label="" @tab-click="handleClick"><!--  -->
<el-button type="primary" size="mini" :disabled="flag" @click="addActive2"></el-button>
<!--  -->
<el-table
        :data="listPublic"
        style="width: 100%;margin-bottom: 20px;"
        border
        stripe
      >
        <el-table-column type="expand" width="50">
        </el-table-column>
        <el-table-column type="index" label="#" width=""> </el-table-column>
        <el-table-column prop="attr_name" label="" width="">
        </el-table-column>
        <!--  -->
        <el-table-column label="" prop="attr_id" width="300">
          <template slot-scope="scope">
            <!--  -->
            <el-button size="mini" type="primary" icon="el-icon-edit" @click="update2(scope.row)"></el-button>
            <!--  -->
            <el-button size="mini" type="danger" icon="el-icon-delete"  @click="del(scope.row.attr_id)"></el-button>
            </template>
        </el-table-column>
      </el-table>
</el-tab-pane>
</el-tabs>
    </el-card>
     <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <!--  -->
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <div slot="footer" class="dialog-footer">
        <!-- ref!!! -->
        <el-button @click="resetForm('ruleForm')"> </el-button>
        <el-button type="primary" @click="submitForm('ruleForm')"
          > </el-button
        >
      </div>
    </el-dialog>
     <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible2">
      <!--  -->
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <div slot="footer" class="dialog-footer">
        <!-- ref!!! -->
        <el-button @click="resetForm('ruleForm')"> </el-button>
        <el-button type="primary" @click="submitForm2('ruleForm')"
          > </el-button
        >
      </div>
    </el-dialog>
   <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisibleUpdate">
      <!--  -->
      <el-form
        :model="ruleFormUpdate"
        :rules="rules"
        ref="ruleFormUpdate"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="ruleFormUpdate.name"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <div slot="footer" class="dialog-footer">
        <!-- ref!!! -->
        
        <el-button @click="resetForm('ruleFormUpdate')"> </el-button>
        <el-button type="primary" @click="updated"
          > </el-button
        >
      </div>
    </el-dialog>
     <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisibleUpdate2">
      <!--  -->
      <el-form
        :model="ruleFormUpdate"
        :rules="rules"
        ref="ruleFormUpdate"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="ruleFormUpdate.name"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <div slot="footer" class="dialog-footer">
        <!-- ref!!! -->
        
        <el-button @click="resetForm('ruleFormUpdate')"> </el-button>
        <el-button type="primary" @click="updated2"
          > </el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  // 
  name: "demo",
  //  
  props: {},
  // 
  components: {},
  // 
  data() {
    return {
      list: [],
      listActive:[],
      listPublic:[],
      //  
      ruleForm: {
        value: [],
        name: "",
      },
      total: 1000,
      type: "",
      // 
      value:[],
      active:'many',
      public:'only',
      flag:true,
      //  
      dialogFormVisible: false,
      dialogFormVisible2: false,
      // 
      dialogFormVisibleUpdate: false,
      dialogFormVisibleUpdate2: false,
      formLabelWidth: "100px",
       // 
      ruleForm: {
        name: ""
      },
      // 
      ruleFormUpdate: {
        name: "",
        id:0,
        cat_id:0
      },
      // 
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
        ]
      },
      // id
      id:0,
      // 
      xuan:'many',
      cat_id:0
    };
  },
  // 
  computed: {},
  // 
  watch: {},
  // 
  methods: {
    handleClick(tab, event) {
        console.log(tab, event);
      },
      // 
    async getList(num = 1, size = this.total) {
      let { data } = await this.$axios.get(
        `/categories?type=${this.type}&pagenum=${num}&pagesize=${size}`
      );
      this.total = data.data.total;
      this.list = data.data.result;
      console.log(this.list);
    },
    // 
    async handleChange(i){
      console.log(i)
      console.log(this.id)
      if(this.id==0){
        let id = i[i.length-1]
       this.id = id
      }
      console.log(this.id)
      let {data:list} = await this.$axios.get(`/categories/${this.id}/attributes`,{
        params:{
          sel:this.active
        }
      })
      let {data:list2} = await this.$axios.get(`/categories/${this.id}/attributes`,{
        params:{
          sel:this.public
        }
      })
      // console.log(list.data,list2.data)
      this.listActive = list.data
      this.listPublic = list2.data
      // if(this.listActive.length!=0||this.listPublic.length!=0){
        this.flag=false
      // }
    },
  // 
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.submitAdd()
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
     // 
    submitForm2(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.submitAdd2()
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    // 
    async submitAdd(){
      console.log(111)
        let data = await this.$axios.post(`/categories/${this.id}/attributes`,{
          attr_name :this.ruleForm.name,
          attr_sel:this.active,
          attr_vals:''
        })
        this.$message({
          message:'',
          duration: 1000,
          type:'success'
        })
        console.log(data)
        this.dialogFormVisible = false;
        // 
        this.ruleForm.name=''
        this.handleChange(this.id)
    }
    ,
     // 
    async submitAdd2(){
        let data = await this.$axios.post(`/categories/${this.id}/attributes`,{
          attr_name :this.ruleForm.name,
          attr_sel:this.public,
          attr_vals:''
        })
        console.log(data)
        this.dialogFormVisible2 = false;
        this.ruleForm.name=''
        this.handleChange(this.id)
    }
    ,
     // 
    async addActive() {
      this.dialogFormVisible = true;
    },
    // 
    async addActive2() {
      this.dialogFormVisible2 = true;
    },
     // 
    resetForm(formName) {
      this.dialogFormVisible2 = false;
      this.dialogFormVisible = false;
      this.dialogFormVisibleUpdate2 = false;
      this.dialogFormVisibleUpdate = false;
      this.$refs[formName].resetFields();
    },
  // 
  del(id){
    // 
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          console.log(id);
          this.$axios.delete(`/categories/${this.id}/attributes/${id}`).then((res) => {
            console.log(res);
            // 
             this.handleChange()
          });
          this.$message({
            type: "success",
            message: "!",
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
  },
  // ===========================================================================================================================================
  update(i){
    // console.log(i.id)
    console.log(i)
    this.ruleFormUpdate.id = i.attr_id
    this.ruleFormUpdate.name = i.attr_name
    this.ruleFormUpdate.cat_id = i.cat_id
    this.dialogFormVisibleUpdate=true
  },
  update2(i){
    this.ruleFormUpdate.id = i.attr_id
    this.ruleFormUpdate.name = i.attr_name
     this.ruleFormUpdate.cat_id = i.cat_id
    this.dialogFormVisibleUpdate2=true
  },
  // 
    async updated() {
      console.log(111)
      let { data } = await this.$axios.put(`/categories/${this.ruleFormUpdate.cat_id}/attributes/${this.ruleFormUpdate.id}`, {
        attr_name: this.ruleFormUpdate.name,
        attr_sel: this.active,
      });
      if (data.meta.status == 200) {
        this.$message({
          message: "",
          type: "success",
        });
      }
      console.log(data.data);
      this.dialogFormVisibleUpdate = false;
      this.handleChange()
    },
    // 
    async updated2() {
      console.log(111)
      let { data } = await this.$axios.put(`/categories/${this.ruleFormUpdate.cat_id}/attributes/${this.ruleFormUpdate.id}`, {
        attr_name: this.ruleFormUpdate.name,
        attr_sel: this.public,
      });
      if (data.meta.status == 200) {
        this.$message({
          message: "",
          type: "success",
        });
      }
      console.log(data.data);
      this.dialogFormVisibleUpdate2 = false;
      this.handleChange()
    },
  },
  mounted() {
    this.getList();
    //  console.log('list2',this.list.length)
  },
};
</script>

