.el-row {
  display: flex;
  align-items: center;
  margin: 20px 0;
  .el-cascader {
    width: 250px;
  }
}
.fl{
  width: 100%;
}
.fl>span{
  width: 120px;
  font-size: 14px;
}
</style>
