<template>
<div class="auth_role">
         <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home/hello' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <div class="auth_card">
            <el-card class="box-card">
                <!--  -->
                <template>
                <el-table :data="tableData" border style="width: 100%">
                    <el-table-column type="index" label="#" width="40"></el-table-column>
                    <el-table-column prop="authName" label=""></el-table-column>
                    <el-table-column prop="path" label=""></el-table-column>
                    <el-table-column prop="level" label="">
                        <template slot-scope="scope">
                            <el-tag v-if="scope.row.level === '0'"></el-tag>
                            <el-tag type="success" v-else-if="scope.row.level === '1'"></el-tag>
                            <el-tag type="warning" v-else="scope.row.level === '2'"></el-tag>
                        </template>
                    </el-table-column>
                </el-table>
                </template>
            </el-card>
        </div>
</div>
</template>

<script>
exportdefault{
data(){
return{
            tableData: []
};
},
created(){

},
mounted(){
        this.ajaxquan()
},
methods:{
       async ajaxquan(){
           let {data:res} =await this.$axios.get(`/rights/list`)
           console.log(res)
           this.tableData = res.data
        }
}
};
</script>

<stylescoped>
.el-breadcrumb{
    cursor: pointer;
}
</style>
