<template>
  <div cote_shop>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home/hello' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <div class="shop_card">
      <el-card class="box-card">
        <!--  -->
        <div class="search_shp">
          <div class="search">
            <el-input
              placeholder=""
              @clear="ajaxshop"
              clearable
              v-model="userParams.query"
              class="input-with-select"
            >
              <el-button slot="append" @click="ajaxshop" icon="el-icon-search"></el-button>
            </el-input>
          </div>
          <!--  -->
          <div class="search_btn">
            <el-button type="primary"></el-button>
          </div>
        </div>
        <!--  -->
        <div class="shop_sta">
          <template>
            <el-table :data="tableData" border style="width: 100%">
              <el-table-column type="index" label="#" width="40" class="table1"></el-table-column>
              <el-table-column prop="goods_name" label="" width="700"></el-table-column>
              <el-table-column prop="goods_price" label="()" width="110"></el-table-column>
              <el-table-column prop="goods_weight" label="" width="90"></el-table-column>
              <el-table-column prop="add_time" label="">
                <template slot-scope="scope">{{ scope.row.add_time | timefn }}</template>
              </el-table-column>
              <el-table-column label="">
                <template slot-scope="scope">
                  <el-button
                    type="primary"
                    size="mini"
                    icon="el-icon-edit"
                    circle
                    @click="shopedit(scope.row)"
                  ></el-button>
                  <el-button
                    type="danger"
                    size="mini"
                    icon="el-icon-delete"
                    circle
                    @click="shopdelete(scope.row.goods_id)"
                  ></el-button>
                </template>
              </el-table-column>
            </el-table>
          </template>
        </div>
        <!--  -->
        <div class="shop_pagina">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="userParams.pagenum"
            :page-sizes="[10, 15, 20, 25]"
            :page-size="userParams.pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="totalnum"
          ></el-pagination>
        </div>
        <!--  -->
        <div class="shop_edit">
          <el-dialog title="" :visible.sync="dialogVisible" width="50%">
            <el-form label-width="80px">
              <el-form-item label="">
                <el-input v-model="shopParams.goodsname"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="shopParams.goodsprice"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="shopParams.goodswith"></el-input>
              </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
              <el-button @click="dialogVisible = false"> </el-button>
              <el-button type="primary" @click="addedit()"> </el-button>
            </span>
          </el-dialog>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialogVisible: false,
      tableData: [],
      list: [],
      userParams: {
        query: "",
        pagenum: 1,
        pagesize: 10
      },
      totalnum: 0,
      dialogVisible: false,
      shopParams:{
        goodsname: "",
        goodsprice: "",
        goodswith: "",
        goodsid:"",
        goodsnum:''
      }
    };
  },
  created() {},
  mounted() {
    this.ajaxshop();
  },
  methods: {
    //
    async ajaxshop() {
      let { data: res } = await this.$axios.get("/goods", {
        params: this.userParams
      });
      console.log(res);
      this.tableData = res.data.goods;
      this.totalnum = res.data.total;
      console.log(this.tableData);
    },
    //
    handleSizeChange(size) {
      console.log(size);
      this.userParams.pagesize = size;
      this.ajaxshop();
    },
    handleCurrentChange(page) {
      console.log(page);
      this.userParams.pagenum = page;
      this.ajaxshop();
    },
    // 
    shopedit(arr) {
      this.dialogVisible = true;
      this.shopParams.goodsid = arr.goods_id;
      this.shopParams.goodsname = arr.goods_name;
      this.shopParams.goodsprice = arr.goods_price;
      this.shopParams.goodswith = arr.goods_weight;
      this.shopParams.goodsnum = arr.goods_number;
      console.log(arr);
    },
    // 
    async addedit() {
      let { data: res } = await this.$axios.put(`/goods/`+ this.shopParams.goodsid, {
        "goods_name": this.shopParams.goodsname,
        "goods_price": this.shopParams.goodsprice,
        "goods_weight": this.shopParams.goodswith,
        "goods_number":this.shopParams.goodsnum
      });
      console.log(res);
      this.$message({
        message: "",
        type: "success",
        duration: 1000
      });
      this.ajaxshop();
      this.dialogVisible = false;
    },
    // 
    shopdelete(id) {
      console.log(id)
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "!"
          });
          this.$axios.delete("/goods/" +id).then((res)=>{
            console.log(res)
            this.ajaxshop()
          })
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    }
  }
};
</script>

<stylescoped>
.el-dialog .el-form {
  bottom: 120px;
  position: static;
}
.search_shp {
  width: 100%;
  display: flex;
}
.shop_sta .el-table {
  font-size: 11px;
}
.shop_sta .table1 {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.search {
  width: 35%;
  display: flex;
  margin-right: 15px;
}
</style>
